	import uniIcons from '../../components/uni-icons/uni-icons.vue';
	import uniStatusBar from '../../components/uni-status-bar/uni-status-bar.vue';
	import miniNavBar from '../../components/common/mini-nav-bar.vue';
	import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
	import uploadImage from "../../common/upload-image.vue";
	import uniPopup from '../../components/uni-popup/uni-popup.vue';
	
	let changeLook = ["所有人可见", "仅自己可见"];
	export default {
		components:{
			uniIcons,
			uniStatusBar,
			miniNavBar,
			uniNavBar,
			uploadImage,
			uniPopup 
		},
		data(){
			return {
				isGet:false,
				content: '顶部弹 popup',
				type: '',
				
				yinsi:"所有人可见",
				text:"",
				imageList:[]
			}
		},
		/* 监听返回 */
		onBackPress: (res) => {
			if(true){
				uni.showModal({
					content: '是否保存为草稿？',
					cancelText: '不保存',
					confirmText: '保存',
					success: res => {
						if (res.confirm){
							console.log("保存")
						} else{
							console.log("不保存")
						}
						this.isGet = true;
						uni.navigateBack({
							delta:1
						});
					}
				});
			}
			console.log("点击返回按钮", res) 
			return true;
		}, 
		methods: {	
			// hidePopup(){
			// 	this.
			// },
			change(e) {	
				console.log('是否打开:' + e.show)
			},
			togglePopup(type, open) {
				switch (type) {
					case 'top':
						this.content = '顶部弹出 popup'
						break
			
					case 'bottom':
						this.content = '底部弹出 popup'
						break
					case 'center':
						this.content = '居中弹出 popup'
						break
				}
				this.type = type
				this.$nextTick(() => {
					this.$refs['show' + open].open()
				})
			},
			upload(arr){
				this.imageList = arr
			},
			changeLook(){
				uni.showActionSheet({
					itemList:changeLook, 
					success: res => { 
						console.log("选择的是",res)
						this.yinsi = changeLook[res.tapIndex]
					}
				});
			},
			back(e){
				uni.navigateBack({
					delta: 1
				});
			},
			fabu(e){
				console.log(e)
				console.log("跳转发布页面",e)
				this.$refs.popup.open()
				// uni.navigateTo({
				// 	url: '../fabu/fabu'
				// });
			}
			
 		}
	}
